  UPDATE_EMPLOYEE: () => {
    return `${Ipconfig.BACKEND_IP()}/deskemployee/updateemp`
  },
  // delete employee
  DELETE_EMPLOYEE: (id) => {
    return `${Ipconfig.BACKEND_IP()}/deskemployee/deleteemp/${id}`
  },

